header {
    display: flex;
    justify-content: space-between;
}

.button--burger {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    cursor: pointer;
    .burger-top,
    .burger-bottom {
        height: 2.5rem;
        width: 2.5rem;
    }
    .burger-middle {
        height: 2.5rem;
        width: 2rem;
    }

  }
